"use client";

import LogoutButton from "@/app/Components/LogoutButton/LogoutButton";
import {
  ChartPieIcon,
  CursorArrowRaysIcon,
  SquaresPlusIcon,
  PhoneIcon,
  FingerPrintIcon,
  ArrowPathIcon,
} from "@heroicons/react/24/outline";
import Link from "next/link";
import MobileView from "@/app/Components/mobile/MobileView";
import { useState, useEffect } from "react";

export default function AdminDashboard() {
  const [currentTime, setCurrentTime] = useState(new Date().toLocaleString());

  useEffect(() => {
    const timer = setInterval(() => {
      setCurrentTime(new Date().toLocaleString());
    }, 1000);
    return () => clearInterval(timer);
  }, []);

  return (
    <div className="flex min-h-screen bg-gray-100">
      {/* Sidebar */}
      <header className="bg-white w-64 h-screen fixed left-0 top-0 overflow-y-auto border-r border-gray-200 shadow-sm">
        <nav aria-label="Global" className="flex flex-col justify-between h-full p-6">
          <div>
            <div className="flex lg:flex-1">
              <a href="#" className="-m-1.5 p-1.5">
                <span className="sr-only">Star-Software</span>
                <h1 className="text-2xl font-bold text-gray-900">Star-Software</h1>
              </a>
            </div>
            <div className="flex flex-col gap-y-4 mt-8">
              {/* Navigation Links */}
              <Link href="/Components/Products" className="flex items-center gap-3 text-sm font-semibold text-gray-700 hover:bg-gray-100 p-3 rounded-lg transition-all duration-300">
                <ChartPieIcon className="h-5 w-5 text-gray-500" /> Products
              </Link>
              <Link href="/Components/Sales" className="flex items-center gap-3 text-sm font-semibold text-gray-700 hover:bg-gray-100 p-3 rounded-lg transition-all duration-300">
                <CursorArrowRaysIcon className="h-5 w-5 text-gray-500" /> Sales
              </Link>
              <Link href="/Components/Purchase" className="flex items-center gap-3 text-sm font-semibold text-gray-700 hover:bg-gray-100 p-3 rounded-lg transition-all duration-300">
                <SquaresPlusIcon className="h-5 w-5 text-gray-500" /> Purchases
              </Link>
              <Link href="/Components/Contacts" className="flex items-center gap-3 text-sm font-semibold text-gray-700 hover:bg-gray-100 p-3 rounded-lg transition-all duration-300">
                <PhoneIcon className="h-5 w-5 text-gray-500" /> Contacts
              </Link>
              <Link href="/Components/Productsetting" className="flex items-center gap-3 text-sm font-semibold text-gray-700 hover:bg-gray-100 p-3 rounded-lg transition-all duration-300">
                <FingerPrintIcon className="h-5 w-5 text-gray-500" /> Product Setting
              </Link>
              <Link href="/Components/Report" className="flex items-center gap-3 text-sm font-semibold text-gray-700 hover:bg-gray-100 p-3 rounded-lg transition-all duration-300">
                <ArrowPathIcon className="h-5 w-5 text-gray-500" /> Report
              </Link>
              <div className="mt-6">
                <LogoutButton />
              </div>
            </div>
          </div>
        </nav>
      </header>

      {/* Main Content */}
      <main className="flex-1 ml-64 p-8">
        <div className="bg-white p-8 rounded-lg shadow-sm border border-gray-100">
          <h1 className="text-3xl font-bold text-gray-900 mb-8">Admin Dashboard</h1>
          <h2 className="text-xl font-semibold text-gray-700 mb-6">Welcome, Admin!</h2>
          <p className="text-gray-600">Manage your products, sales, purchases, and more from this dashboard.</p>
          <div className="mt-6 bg-gray-50 p-6 rounded-lg shadow-sm border border-gray-200">
            <h3 className="text-lg font-semibold text-gray-800">Live Date and Time</h3>
            <p className="text-gray-600 font-mono text-xl mt-2">{currentTime}</p>
          </div>
        </div>
        {/* Additional Content */}
        <div className="mt-8 grid grid-cols-1 md:grid-cols-2 lg:grid-cols-3 gap-6">
          <div className="bg-white p-6 rounded-lg shadow-md border border-gray-200">
            <h3 className="text-xl font-semibold text-gray-900">Recent Sales</h3>
            <p className="text-gray-600">View the latest sales transactions.</p>
          </div>
          <div className="bg-white p-6 rounded-lg shadow-md border border-gray-200">
            <h3 className="text-xl font-semibold text-gray-900">New Products</h3>
            <p className="text-gray-600">Check out the recently added products.</p>
          </div>

        </div>
        {/* Footer */}
        <footer className="mt-12 text-center text-gray-500">© 2025 Star-Software. All rights reserved.</footer>
      </main>
      {/* Mobile View */}
      <MobileView />
    </div>
  );
}
